:root {
  --brand-deep: #142168;
  --brand-deep-alt: #0f1740;
  --brand-accent: #1B5FC1;
  --muted: #6b7280;
  --light: #f6f6f6;
  --max-width: 1200px;
}

/* page reset basics */
* { box-sizing: border-box; }
html, body, #root { height: 100%; }
body {
  margin: 0;
  font-family: Inter, ui-sans-serif, system-ui, -apple-system, 'Segoe UI', Roboto, 'Helvetica Neue', Arial;
  color: #111827;
  background: white;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1.45;
}

/* container */
.container {
  width: 100%;
  max-width: var(--max-width);
  margin: 0 auto;
  padding: 0 24px;
}

.section-title {
  position: relative;
  display: inline-block;
  padding-bottom: 8px;
}
.section-title::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  height: 3px;
  width: 60%;
  background: linear-gradient(90deg, #1b5fc1, #142168);
  border-radius: 2px;
}
